@import "normalize.css";

:root {
    --color-black: #141517;
    --color-gray: #f2f4f7;
    --color-white: #fff;
    --color-tint: #0097e6;
    --color-text-default: var(--color-black);
    --color-text-links: var(--color-tint);
    --color-background-default: var(--color-gray);
}

@font-face {
    font-family: Gilroy-Light;
    font-display: swap;
    src: local('Gilroy-Light'), local('Gilroy'), url("../fonts/Gilroy-Light.otf");
}

@font-face {
    font-family: Gilroy-ExtraBold;
    font-display: swap;
    src: local('Gilroy-ExtraBold'), local('Gilroy'), url("../fonts/Gilroy-ExtraBold.otf");
}

html {
    font-family: Gilroy-Light, sans-serif;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
    font-variation-settings: "wdth" 100;
    color: var(--color-text-default);
    background-color: var(--color-background-default);
    font-size: 16px;
}

a {
    color: var(--color-text-links);
    text-decoration: underline;
    text-underline-offset: 6px;
    transition: opacity 200ms ease;
    padding-right: 4px;
    &:hover {
        opacity: 0.6;
    }
}

header {
    display: flex;
    justify-content: space-between;
    padding: 16px;
    backdrop-filter: blur(4px);
    position: sticky;
    top: 0;

    a,
    a:visited,
    a:link {
        text-decoration: none;
        color: var(--color-text-default);
        &:hover {
            opacity: 1;
        }
    }

    nav {
        display: flex;
        gap: 16px;
        align-items: center;
    }

    .header-index {
        font-size: 1.5rem;
        font-family: Gilroy-ExtraBold, sans-serif;
    }

    .header-link {
        padding: 6px 0;
        border-bottom: 2px solid transparent;
        transition: border-bottom 400ms ease;

        &:hover {
            border-bottom: 2px solid var(--color-text-links);
        }
    }
}

main {
    max-width: 720px;
    margin: 0 auto;
    padding: 0 16px;
}

#about {
    --icon-size: 48px;

    h1 {
        font-size: 2rem;
        line-height: 1.5;
    }

    p {
        font-size: 1.125rem;
        line-height: 1.5;
    }

    ul {
        align-items: center;
        display: flex;
        flex-wrap: wrap;
        gap: calc(var(--icon-size) / 4);
        height: 100%;
        justify-content: center;
        margin: 32px 0;
        padding: 0;
        list-style: none;

        a {
            background-size: var(--icon-size) var(--icon-size);
            display: block;
            padding: 0;
            height: var(--icon-size);
            width: var(--icon-size);
        }
    }
}

.github {
    background-image: url('data:image/png;base64,')
}

.linkedin {
    background-image: url('data:image/png;base64,')
}

.email {
    background-image: url('data:image/png;base64,')
}

@media screen and (max-width: 480px) {
    html {
        font-size: 12px;
    }
}
